document.addEventListener('DOMContentLoaded',function() {    const el = document.getElementById('gallery');    const init = function() {        const thumb = el.getElementsByClassName('js-thumb');        const big = el.getElementsByClassName('js-big')[0];        const enlarge = function(e) {            const img_big = e.currentTarget.getAttribute('href');            big.setAttribute('src', img_big);                        for (let i = 0; i < thumb.length; i ++) {                thumb[i].classList.remove('is-active');            }                        e.currentTarget.classList.add('is-active');            e.preventDefault() ? e.preventDefault() : e.preventDefault = false;        };        for (let i = 0; i < thumb.length; i ++) {            thumb[i].addEventListener('click', enlarge);        }    };    el ? init() : false;}, false);